import { StrictMode } from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter as Router } from 'react-router-dom';

import App from './App';

const rootElement = document.getElementById('app');
const root = createRoot(rootElement);

root.render(
	<StrictMode>
		<Router>
			<App />
		</Router>
	</StrictMode>
);


